.profile-wrap {
    width: 12rem;
    height: 12rem;
    margin: 0 auto;
    position: relative;
}

.profile-wrap img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
}

.btn-profile {
    position: absolute;
    bottom: 0;
    right: 0;
}

.btn-profile input[type="file"] {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
}

.btn-profile label {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background: var(--main-color) url(../assets/icon-image.svg) no-repeat 50% 50% / 2.4rem;
    position: absolute;
    cursor: pointer;
}
